.homeSection{
    height: 88vh;
    background-color: $color-2;
    margin-top: -5rem;
    color: $color-3;
    @media screen and (min-width: 768px) {
        margin: 0px;
        height: 100vh;
        }
}
.presentation{
    @include flex (center, column, center);
    height: 100%;
    margin: 5rem 1rem;
    @media screen and (min-width: 768px) {
        @include flex (center, column, center);
        align-self: center;
        margin: 0rem;
        }
    &_sectionAbout{
        @media screen and (min-width: 426px) {
            margin-bottom: 2rem;  
          }
        @media screen and (min-width: 768px) {
          margin-bottom: 5rem;  
        }
    }
}
.imgModerate-container{
margin: 0rem auto 5rem auto;
}
.imgModerate{
position: relative;
border-radius: 50%;
box-shadow: 0 0 0 4px $color-2, 0 0 0 9px $color-1;
width: 40vw;
@media screen and (min-width: 426px) {
    width: 30vw;
    }
@media screen and (min-width: 768px) {
    width: 20vw;
    }
}
.moderateName-container{
    @include flex (center, column, center);
    margin: 0rem 2rem;
    font-family: $font-3;
    @media screen and (min-width: 768px) {
        margin: auto 5rem; 
        }
}
.moderateName{
    font-size: 1.8rem;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: .2rem;
    @media screen and (min-width: 768px) {
        font-size: 2.5rem;
        }
}
.moderateFunction{
    font-size: 1.5rem;
    @media screen and (min-width: 768px) {
        font-size: 2rem;
        }
}
.abouteMe{
    font-family: $font-1;
    font-size: 1.6rem;
    padding: 5px;
    margin: 2rem 0rem;
    border-right: 1px solid $color-3;
    border-bottom: 1px solid $color-3;
    @media screen and (min-width: 768px) {
        font-size: 2rem;
        }
}
.homeFooter{
    @include flex(space-around, row, center);
    font-size: 1.4rem;
    margin-top: 5rem;
    &> a:hover{
        color: $color-1;
    }
}